<template>
    <Loading :isActive="fetching"></Loading>
    <AlertBox :message="message" @clrMessage=" message ='' "/>
    <section class="min-h-screen mt-8 md:mt-12 text-blue relative bg-[#685FCE] py-4">  
        <div class="mt-8 px-4 md:px-14 md:text-end">
            <input class="w-full md:w-1/3 p-2 rounded-md" v-model="searchKeyword" @keyup.enter="searchingItem" placeholder="search item with code">     
        </div>     
        <div class="mt-4 flex flex-wrap justify-center gap-y-5 md:gap-x-5">    
            <TransitionRoot as="template" :show="open">
                <Dialog as="div" class="relative z-10" @close="open = false">
                <TransitionChild as="template" enter="ease-out duration-300" enter-from="opacity-0" enter-to="opacity-100" leave="ease-in duration-200" leave-from="opacity-100" leave-to="opacity-0">
                    <div class="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" />
                </TransitionChild>

                <div class="fixed inset-0 z-10 overflow-y-auto">
                    <div class="flex min-h-full items-center justify-center p-6 text-center sm:items-center sm:p-0">
                    <TransitionChild as="template" enter="ease-out duration-300" enter-from="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95" enter-to="opacity-100 translate-y-0 sm:scale-100" leave="ease-in duration-200" leave-from="opacity-100 translate-y-0 sm:scale-100" leave-to="opacity-0 translate-y-4 sm:translate-y-0 sm:scale-95">
                        <DialogPanel class="relative transform overflow-hidden rounded-lg bg-grey-500 text-left shadow-xl transition-all sm:my-8 sm:w-full sm:max-w-lg">
                        <div class="bg-[#685FCE] px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                            <div class="sm:flex sm:items-start">
                            <div class="mx-auto flex h-12 w-12 flex-shrink-0 items-center justify-center rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10">
                                <ExclamationTriangleIcon class="h-6 w-6 text-red-600" aria-hidden="true" />
                            </div>
                            <div class="mt-3 text-center sm:mt-0 sm:ml-4 sm:text-left">
                                <DialogTitle as="h3" class="text-lg font-medium leading-6 text-white">Delete</DialogTitle>
                                <div class="mt-2">
                                <p class="text-sm text-white">Are you sure you want to remove {{itemName.toLowerCase()}} </p>
                                </div>
                            </div>
                            </div>
                        </div>
                        <div class="bg-[#685FCE] px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
                            <button type="button" class="inline-flex w-full justify-center rounded-md border border-transparent bg-red-600 px-4 py-2 text-base font-medium text-white shadow-sm hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-red-500 focus:ring-offset-2 sm:ml-3 sm:w-auto sm:text-sm" @click="accept">Yes</button>
                            <button type="button" class="mt-3 inline-flex w-full justify-center rounded-md border border-gray-300 bg-white px-4 py-2 text-base font-medium text-gray-700 shadow-sm hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-indigo-500 focus:ring-offset-2 sm:mt-0 sm:ml-3 sm:w-auto sm:text-sm" @click="deny" ref="cancelButtonRef">No</button>
                        </div>
                        </DialogPanel>
                    </TransitionChild>
                    </div>
                </div>
                </Dialog>
            </TransitionRoot>
            <ModelBox v-for="doc of allItems" :key="doc.id" @increase="counter" @decrease="counter" @editItem="editItem(doc.id)" @deleteItem="popUpDelete(doc.id, doc.data.image_infos.public_id, doc.data.name)" :infos="doc.data" :id="doc.id"/>      
        </div>
        <div ref="bodyScrollTrigger" class="w-full flex justify-center mt-3">            
            <div role="status" v-if="loadingMore">
                <svg aria-hidden="true" class="w-8 h-8 mr-2 text-gray-200 animate-spin dark:text-gray-600 fill-blue-600" viewBox="0 0 100 101" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M100 50.5908C100 78.2051 77.6142 100.591 50 100.591C22.3858 100.591 0 78.2051 0 50.5908C0 22.9766 22.3858 0.59082 50 0.59082C77.6142 0.59082 100 22.9766 100 50.5908ZM9.08144 50.5908C9.08144 73.1895 27.4013 91.5094 50 91.5094C72.5987 91.5094 90.9186 73.1895 90.9186 50.5908C90.9186 27.9921 72.5987 9.67226 50 9.67226C27.4013 9.67226 9.08144 27.9921 9.08144 50.5908Z" fill="currentColor"/>
                    <path d="M93.9676 39.0409C96.393 38.4038 97.8624 35.9116 97.0079 33.5539C95.2932 28.8227 92.871 24.3692 89.8167 20.348C85.8452 15.1192 80.8826 10.7238 75.2124 7.41289C69.5422 4.10194 63.2754 1.94025 56.7698 1.05124C51.7666 0.367541 46.6976 0.446843 41.7345 1.27873C39.2613 1.69328 37.813 4.19778 38.4501 6.62326C39.0873 9.04874 41.5694 10.4717 44.0505 10.1071C47.8511 9.54855 51.7191 9.52689 55.5402 10.0491C60.8642 10.7766 65.9928 12.5457 70.6331 15.2552C75.2735 17.9648 79.3347 21.5619 82.5849 25.841C84.9175 28.9121 86.7997 32.2913 88.1811 35.8758C89.083 38.2158 91.5421 39.6781 93.9676 39.0409Z" fill="currentFill"/>
                </svg>
                <span class="sr-only">Loading...</span>
            </div>
        </div>
    </section>
</template>

<script>

import Loading from '../components/loading.vue'
import ModelBox from '../components/modelBox.vue';
import AlertBox from '../components/alertBox.vue';
import restApi from '../services/rest.js';
import { Dialog, DialogPanel, DialogTitle, TransitionChild, TransitionRoot } from '@headlessui/vue'
import { ExclamationTriangleIcon } from '@heroicons/vue/24/outline'
import { onMounted, ref, onUnmounted } from 'vue';
import { useRouter, useRoute } from 'vue-router'
export default {
    components: {
        Loading,
        AlertBox,
        ModelBox,
        Dialog,
        DialogPanel,
        DialogTitle,
        TransitionChild,
        TransitionRoot,
        ExclamationTriangleIcon,
    },
    setup(){
        const router = useRouter();
        let allItems = ref([]);
        const open = ref(false);
        const fetching = ref(false);
        const loadingMore = ref(false);
        let message = ref('');
        let searchKeyword = ref('');
        let itemId = null;
        let imgUrl = '';
        let itemName = ref('');
        let limit = 10;
        const bodyScrollTrigger = ref(0);
        const scrollCount = ref(0);
        //getting infos data from local storage at setup stage
        let soldTotalItems = JSON.parse(localStorage.getItem("soldTotalItems")) || [];

        const loadData = async ($state) => {
            if(loadingMore.value) return;
            loadingMore.value = true;
            let res = await restApi.getItem(limit)
            let {data, error} = res.data;
            if(error) {
                loadingMore.value = false;
                return message.value = error;
            };
            allItems.value = data;
            limit += 10;
            loadingMore.value = false;
        }

        const handleScroll = () => {
            const scrollPos = bodyScrollTrigger.value.getBoundingClientRect().top;
            if(scrollPos <= window.innerHeight && !loadingMore.value){
                scrollCount.value++;
                if(scrollCount.value >= 10){
                    loadData();
                    scrollCount.value = 0;
                }
            }
        }

        onMounted(() => {
            loadData();
            window.addEventListener('scroll', handleScroll)
        });

        onUnmounted(() => {
            window.removeEventListener('scroll', handleScroll)
        });

        const popUpDelete = (id, img_url, item_name) => {
            itemId = id;
            imgUrl = img_url;
            itemName.value = item_name
            open.value = true;
        }

        const accept = async () => {
            open.value = false;
            fetching.value = true;
            const res = await restApi.deleteItem(itemId, imgUrl);
            let {meta, data, error} = res.data;
            if(error){
                message.value = error;
                return fetching.value = false;
            }
            allItems.value = allItems.value.filter(doc => doc.id !== meta.id);
            soldTotalItems = soldTotalItems.filter(doc => doc.id !== meta.id);
            localStorage.setItem("soldTotalItems", JSON.stringify(soldTotalItems));
            fetching.value = false;
        }

        const deny = () => {
            open.value = false;
        }

        const editItem = (id) => {
            let item = allItems.value.filter(doc => doc.id === id);
            let {name, sale_price, original_price} = item[0].data;
            router.push({
                name: 'EditPage',
                params: {
                    id: id,
                    name: name,
                    sale_price: sale_price,
                    original_price: original_price,
                }
            })
        }

        const counter = (v) => {
            soldTotalItems = JSON.parse(localStorage.getItem("soldTotalItems")) || [];
            for(let doc of soldTotalItems){
                if(doc.id == v.id){
                    doc.count = v.count;
                    localStorage.setItem("soldTotalItems", JSON.stringify(soldTotalItems));
                    return;
                }
            }      
            soldTotalItems.push(v)
            localStorage.setItem("soldTotalItems", JSON.stringify(soldTotalItems));      
        }

        const searchingItem = async () => {
            loadingMore.value = true;
            let res = await restApi.getSearchItem(searchKeyword.value);
            let {data, error} = res.data;
            if(error){
                loadingMore.value = false;
                return message.value = error;
            }
            allItems.value = data;
            searchKeyword.value = '';
            loadingMore.value = false;
        }

       return{message, loadData, searchKeyword, searchingItem, bodyScrollTrigger, allItems, open,loadingMore, fetching, popUpDelete, deny, accept, itemName, editItem, counter, soldTotalItems}
    }
}
</script>