<template>
    <div class="w-11/12 md:w-[43%] lg:w-[30%]  bg-[#ffffff70] rounded-sm p-3 shadow-md flex self-auto gap-x-4 xsm:gap-x-6 hover:scale-105 transition-all">
       <section class="w-[40%]">
           <AdvancedImage class="rounded-sm" :cldImg="myImage"></AdvancedImage>
       </section>
       <section class="w-2/3 text-start text-white flex flex-col justify-between">
            <div class="text-xs xsm:text-lg md:text-sm 2xl:text-lg">
                <h3>Name : <span>{{infos.name}}</span></h3>
                <h3 class="mt-4 xsm:mt-3">Original Price : <span>{{infos.original_price}}MMK</span></h3>
                <h3 class="mt-4 xsm:mt-3">Sale Price : <span>{{infos.sale_price}}MMK</span></h3>
            </div>
            <div class="flex justify-between">
                <div class="self-end">
                    <button type="button" @click="deleteItem" class="bg-[#FF0000] w-12 xsm:w-16 md:w-12 h-7 xsm:h-10 rounded-sm p-1 text-xs xsm:text-lg leading-none active:bg-[#FF000095] focus:ring-1 focus:ring-[#FF000090]"><i class="fa-sharp fa-solid fa-trash"></i></button>
                    <button type="button" @click="editItem" class="bg-[#00FF94] w-12 xsm:w-16 md:w-12 h-7 xsm:h-10 rounded-sm p-1 text-xs xsm:text-lg leading-none active:bg-[#00FF9495] focus:ring-1 focus:ring-[#00FF9490] ml-2"><i class="fa fa-file-pen"></i></button>
                </div>
                <div class="w-[35%]">
                    <div class="w-full bg-black text-white rounded-sm h-7 xsm:h-10 flex justify-center items-center">
                        <h5>{{count}}</h5> 
                    </div>
                    <div class="text-black mt-2 flex items-center justify-center">
                        <button @click="decrease" type="button" class="bg-[#ffffff] w-[48%] h-7 xsm:h-10 rounded-sm p-1 text-sm leading-none active:bg-slate-200 focus:ring-1 focus:ring-slate-100"><i class="fa fa-minus"></i></button>
                        <button @click="increase" type="button" class="bg-[#ffffff] w-[48%] h-7 xsm:h-10 rounded-sm p-1 text-sm leading-none active:bg-slate-200 focus:ring-1 focus:ring-slate-100 ml-2"><i class="fa fa-plus"></i></button>
                    </div>
                </div>
            </div>
       </section>
    </div>
</template>

<script>
import { ref } from '@vue/reactivity'
import {AdvancedImage} from '@cloudinary/vue'
import cld from '../cloudinary/config.js'
import { fill, fillPad, fit, scale } from '@cloudinary/transformation-builder-sdk/actions/resize'

export default {
    props: ['infos', 'id'],
    components: {
        AdvancedImage,
    },
    setup(props, {emit}){
        let count = ref(0);
        const myImage = cld.image(props.infos.image_infos.public_id);
        myImage.resize(scale().width(200).height(270));
        let localData = JSON.parse(localStorage.getItem("soldTotalItems"));
        if(localData){
            localData.forEach(doc => {
                doc.id == props.id ? count.value = doc.count : '' ;
            })
        }
        const deleteItem = () => {
            emit('deleteItem')
        }

        const editItem = () => {
            emit('editItem')
        }

        const increase = () => {
            count.value++;
            emit('increase', {count: count.value, id: props.id, data: props.infos});
        }

        const decrease = () => {
            if(count.value <= 0) return;
            count.value--;
            emit('decrease', {count: count.value, id: props.id, data: props.infos});
        }
        return {count, deleteItem, myImage, editItem, increase, decrease}
    }
}
</script>
