import http from '../../http-common.js'

class Rest{

    addItem(infos){
        return http.post('/items/add/', infos)
    }
    
    addMonthlySelling(infos){
        return http.post('/monthly/add/', infos)
    }
   
    getItem(limit){
        return http.get('/all_items/',{
            params: {
                limit
            }
        });
    }
    
    getMonthlyLists(){
        return http.get('/monthly_lists/');
    }

    getSearchItem(queryName){
        return http.get('/search_items/', {
            params: {
                queryName
            }
        })
    }

    deleteItem(id,img_url){
        return http.delete(`/delete/item/`,{
            params:{
                id,
                img_url
            }
        })
    }

    deleteMonthlyLists(){
        return http.delete('/monthly_lists/')
    }

    updateItem(id, infos){
        return http.patch(`/edit/${id}`, infos)
    }
}

export default new Rest();
