<template>
    <div>
        <div class="mt-3">
            <h2>
                <button type="button" @click="show = !show" class="flex items-center rounded-t-md bg-[#ffffff70] justify-between w-full p-5 font-medium border border-b-0 border-gray-200 focus:ring-4 focus:ring-gray-20 hover:bg-gray-100">
                    <span class="text-black">{{infos.date}}</span>
                    <span class="text-black">PROFIT : {{infos.totalProfit}}Ks</span>
                    <svg class="w-6 h-6 rotate-180 shrink-0 text-black" fill="currentColor" viewBox="0 0 20 20" xmlns="http://www.w3.org/2000/svg"><path fill-rule="evenodd" d="M5.293 7.293a1 1 0 011.414 0L10 10.586l3.293-3.293a1 1 0 111.414 1.414l-4 4a1 1 0 01-1.414 0l-4-4a1 1 0 010-1.414z" clip-rule="evenodd"></path></svg>
                </button>
            </h2>
            <div v-show="show" class="bg-[#ffffff70] hover:bg-gray-100 rounded-b-md">
                <div class="px-5 font-light border">
                    <HistoryList v-for="item of itemList" :key="item.id" :name="item.data.name" :count="item.count" :price="item.data.sale_price"></HistoryList>
                </div>
                <div class="flex border justify-between px-5 py-3 bg-green-500 rounded-b-md">
                    <h3>Total</h3>
                    <h3>{{infos.totalSellingPrice}}Ks</h3>
                </div>
            </div>
        </div>
    </div>  
</template>

<script>
import { ref } from '@vue/reactivity'
import HistoryList from './historyList'
export default {
    props:['infos'],
    components: { HistoryList },
    setup (props) {
        let show = ref(false);
        let itemList = ref([]);
        itemList.value = props.infos.soldTotalItems; 
        return {show, itemList}
    }
}
</script>

