<template>
    <transition name="model">
        <div v-if="message" class="fixed top-0 w-full h-full bg-[#DDDDDD60] z-50" @click="clrMessage">
            <div class="absolute top-[5%] rounded-md left-[5%] right-[5%] md:left-[35%] md:right-[35%] text-center flex items-center bg-[#fff] px-3 py-4 space-between shadow-md">
                <h2>{{message}}</h2>
            </div>
        </div>
    </transition>
</template>

<script>
    export default {
        props: ['message'],
        setup(props, {emit}){
           let clrMessage = () => {
              emit('clrMessage')
           }
           return {clrMessage}
        }
    }
</script>

<style scoped>
    .model-enter-from{
        opacity: 0;
    }
    .model-enter-to{
        opacity: 1;
    }
    .model-enter-active{
        transition: all 0.5s ease;
    }
    .model-leave-from{
        opacity: 1;
    }
    .model-leave-to{
        opacity: 0;
    }
    .model-leave-active{
        transition: all 0.5s ease;
    }
</style>