
import { ref } from '@vue/reactivity';
export default {
    setup(){
        let activeNav = ref(true);
        let hideNavItem = ref(true);
        let searchKeyword = ref('');
        const toggleNav = () => {
            activeNav.value = !activeNav.value
            hideNavItem.value = true;
        }
        return {toggleNav, activeNav, hideNavItem, searchKeyword}
    }
}
