<template>
    <Loading :isActive="fetching"></Loading>
    <AlertBox :message="message" @clrMessage=" message ='' "/>
    <div class="min-h-screen mt-14 md:mt-18 bg-[#685FCE]">
        <div class="rounded-md px-4 py-6">
                <div class="relative">
                    <input v-model="name" type="text" id="floating_filled" class="block rounded-t-lg rounded-md px-2.5 pb-2.5 pt-5 w-full md:w-[30%] text-md peer" placeholder=" " />
                    <label for="floating_filled" class="absolute text-md  duration-300 transform -translate-y-4 scale-75 top-4 z-10 origin-[0] left-2.5  peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-4">Item Name</label>
                </div>
                <div class="relative mt-6">
                    <input v-model="original_price" type="number" id="floating_filled" class="block rounded-t-lg rounded-md px-2.5 pb-2.5 pt-5 w-full md:w-[30%] text-md peer" placeholder=" " />
                    <label for="floating_filled" class="absolute text-md  duration-300 transform -translate-y-4 scale-75 top-4 z-10 origin-[0] left-2.5  peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-4">Original Price</label>
                </div>
                <div class="relative mt-6">
                    <input v-model="sale_price" type="number" id="floating_filled" class="block rounded-t-lg rounded-md px-2.5 pb-2.5 pt-5 w-full md:w-[30%] text-md peer" placeholder=" " />
                    <label for="floating_filled" class="absolute text-md  duration-300 transform -translate-y-4 scale-75 top-4 z-10 origin-[0] left-2.5  peer-placeholder-shown:scale-100 peer-placeholder-shown:translate-y-0 peer-focus:scale-75 peer-focus:-translate-y-4">Sale Price</label>
                </div>              
                <!-- <div class="flex items-center justify-center w-4/12 md:w-1/6 mt-6">
                    <label for="dropzone-file" class="flex flex-col items-center justify-center w-full border-2 border-gray-300 border-dashed rounded-lg cursor-pointer bg-gray-50 hover:bg-gray-100 ">
                        <div v-if="!image_file" class="flex flex-col items-center justify-center pt-5 pb-6">
                            <svg aria-hidden="true" class="w-10 h-10 mb-3 text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12"></path></svg>
                            <p class="mb-2 text-sm text-gray-500 dark:text-gray-400">Select Image</p>
                        </div>
                        <div v-else class="flex flex-col items-center justify-center pt-5 pb-6">
                            <i class="fa fa-image text-4xl mb-3 text-gray-400"></i>
                            <p class="mb-2 text-sm text-gray-500 dark:text-gray-400">Selected</p>
                        </div>
                        <input id="dropzone-file" @change="fileUpload($event)" type="file" accept="image/*" class="hidden" />
                    </label>
                </div>  -->
                <div class="mt-6 flex self-start">
                    <button @click="update" class="self-start px-4 py-2 bg-[#00FF94] rounded-sm w-24 active:bg-[#00FF9495] focus:ring-1 focus:ring-[#00FF9490]">Update</button>
                </div>
        </div>
    </div>
</template>

<script>
import {onMounted} from 'vue'
import { ref } from '@vue/reactivity'
import { useRouter, useRoute } from 'vue-router'
import AlertBox from '../components/alertBox.vue';
import Loading from '../components/loading.vue'
import restApi from '../services/rest.js'

export default {
    components: {
        AlertBox,
        Loading
    },
    setup () {
        const router = useRouter();
        const route = useRoute();
        const name = ref('');
        const original_price = ref(undefined);
        const sale_price = ref(undefined);
        let id = '';
        let message = ref('');
        const fetching = ref(false);

        onMounted(() => {
            name.value =  route.params.name,
            original_price.value = route.params.original_price,
            sale_price.value = route.params.sale_price,
            id = route.params.id
        })

        let update = async () => {
            fetching.value = true;
            try{
                let res = await restApi.updateItem(id, {
                    name: name.value,
                    original_price: original_price.value,
                    sale_price: sale_price.value
                });
                let {meta, error} = res.data;   
                if(error){
                    throw new Error(error)
                }
                router.replace({
                    path: `/edit/${id}/${name.value}/${original_price.value}/${sale_price.value}`,   
                });
                fetching.value = false
            }catch(err){
                fetching.value = false;
                message.value = err.message;
            }
        }

        return {name, original_price, sale_price, fetching, message, update}
    }
}
</script>

