<template>
    <Loading :isActive="fetching"></Loading>
    <AlertBox :message="errormsg" @clrMessage="errormsg = ''"></AlertBox>
    <section class="min-h-screen mt-14 md:mt-20 bg-[#685FCE] p-4">
        <div class="flex flex-wrap md:flex-nowrap w-full justify-around">
            <div class="bg-[#ffffff70] text-white w-full md:w-[48%] py-8 space-y-3 shadow-md rounded-md">
                <h1 class="text-2xl md:text-4xl font-bold tracking-wider">TOTAL</h1>
                <h2 class="text-xl md:text-2xl tracking-wider">{{totalSellingPrice}}Ks</h2>
            </div>
            <div class="bg-[#ffffff70] text-white w-full md:w-[48%] mt-8 md:mt-0 py-8 space-y-3 shadow-md rounded-md">
                <h1 class="text-2xl md:text-4xl font-bold tracking-wider">PROFIT</h1>
                <h2 class="text-xl md:text-2xl tracking-wider">{{totalProfit}}Ks</h2>
            </div>
        </div>
        <div v-if="soldTotalItems.length > 0" class="md:px-3 mt-7 lg:mt-10">
            <h1 class="text-3xl text-white text-start">TODAY SOLD</h1>              
            <div class="w-full flex justify-items-start mt-4"><Datepicker class="px-2 py-2 rounded-sm bg-[#ffffff70] text-white w-[6.5rem] cursor-pointer" v-model="picked"/></div>
            <div class="flex flex-col flex-wrap space-x md:gap-6 md:flex-row">
                <RecordBox v-for="item of soldTotalItems" :key="item.id" v-show="item.count > 0" :infos="item"></RecordBox>
            </div>
            <div class="mt-6 flex self-start">
                <button @click="save" class="self-start px-6 py-3 bg-[#00FF94] rounded-sm w-24 active:bg-[#00FF9495] focus:ring-1 focus:ring-[#00FF9490] font-medium">Save</button>
            </div>
        </div>
    </section>
</template>

<script>
import Loading from '../components/loading.vue'
import AlertBox from '../components/alertBox'
import RecordBox from '../components/recordBox'
import Datepicker from 'vue3-datepicker'
import {ref} from '@vue/reactivity'
import restApi from '../services/rest.js';
export default {
    components: { RecordBox, Datepicker, Loading, AlertBox},
    setup () {
        let soldTotalItems = ref([]);
        const picked = ref(new Date());
        let totalSellingPrice = ref(0);
        let totalOriginalPrice = ref(null);
        let totalProfit = ref(0);
        let fetching = ref(false);
        let errormsg = ref(null);
        soldTotalItems.value = JSON.parse(localStorage.getItem('soldTotalItems')) || [];
        
        soldTotalItems.value.forEach(item => {
            totalSellingPrice.value += item.count * item.data.sale_price; 
            totalOriginalPrice.value += item.count * item.data.original_price;
        });

        totalProfit.value = totalSellingPrice.value - totalOriginalPrice.value;

        const save = async () => {
            if(soldTotalItems.value.length == 0) return;
            fetching.value = true;
            const res = await restApi.addMonthlySelling({soldTotalItems: soldTotalItems.value, date: picked.value.toDateString(), totalProfit: totalProfit.value, totalSellingPrice: totalSellingPrice.value});
            let {error, meta} = res.data;
            if(error){
                fetching.value = false;
                return errormsg.value = error;
            }
            localStorage.removeItem("soldTotalItems");
            soldTotalItems.value = [];
            totalSellingPrice.value = 0;
            totalProfit.value = 0;
            fetching.value = false;
        }

        return {soldTotalItems, picked, totalSellingPrice, totalProfit, save, fetching, errormsg}
    }
}
</script>
