<template>
    <section class="w-full sm:w-auto sm:py-6 sm:px-12 cursor-pointer py-4 mt-4 bg-[#ffffff70] shadow-md space-y-4 text-white rounded-md hover:scale-105 transition-all">
        <h2 class="text-2xl font-semibold">{{name}}</h2>
        <p class="text-2xl">{{count}}</p>
    </section>
</template>

<script>
export default {
    props: ['infos'],
    setup (props) {
        let {name} = props.infos.data;
        let count = props.infos.count;
        return {name, count}
    }
}
</script>